.radio {
  margin: 0;
}

.radio__text {
  font-size: 16px;
  line-height: 18px;
  color: #222222;
  text-transform: uppercase;
  padding-left: 45px;
  cursor: pointer;
  position: relative;
}

.radio__text::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  left: 0;
  background-color: #ffffff;
  border: solid 1px #cdcdcd;
  border-radius: 50%;
}


.radio__input:checked + .radio__text::before {
  background-image: url("../img/radio-dot.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.radio__text:hover::before {
  border: solid 1px #bababa;
}

.radio__text:disabled {
  opacity: 30%;
}

.radio__text:disabled::before {
  opacity: 30%;
}
